//直接引用blog，无需使用Vuex
import blog from '@/api/blog'

export default {
  data () {
    return {
      index:'',
      blogs: [], //首页展示博客列表
      total: 0,
      page: 1,
    }
  },

  created() { //出生获取数据
    //判断this.$route.query.page是否存在
    this.page = parseInt(this.$route.query.page) || 1 //parseInt() 函数可解析一个字符串，并返回一个整数。
    blog.getIndexBlogs({page:this.page}) //精确：获取指定页数博客列表，只得到显示在首页的博客
      .then(res => {
        console.log(res) 
        this.blogs = res.data  //数据存放到blog
        this.total = res.total //数据替换total
        this.page = res.page   //数据替换page
      })
  },
  //精确：获取指定页数博客列表，只得到显示在首页的博客
  /*
  getIndexBlogs({ page=1 } = { page: 1}) {
    return this.getBlogs({ page, atIndex: true })
  },
  */
  
  methods: {
    onPageChange(newPage) {
      blog.getIndexBlogs({page:newPage}) //精确：获取指定页数博客列表，只得到显示在首页的博客
        .then(res => {
          this.blogs = res.data  //数据存放到blog
          this.total = res.total //数据替换total
          this.page = res.page   //数据替换page
          this.$router.push( {path:'/',query:{ page:newPage } } )
        })
    },
  }
}



